( () => {
  const themesNav = document.querySelector<HTMLElement>('.themes-nav');
  if (!themesNav) return;

  const buttons = themesNav.querySelectorAll<HTMLElement>('.themes-nav__list button');
  buttons.forEach(btn => {
    btn.addEventListener('click', () => {
      buttons.forEach(el => el.classList.remove('--active'));
      btn.classList.add('--active');
    });
  });
} )();

(() => {
  const  blogArticles =  document.querySelector<HTMLElement>('.blog-articles');
  if (!blogArticles) return;

  const button = document.querySelector<HTMLElement>('.blog-articles .button');

  button?.addEventListener('click', () => {
    button.classList.add('--is-loading');
  });
})();

document.querySelectorAll<HTMLElement>('.video').forEach(video => {
  const videoElem = video.querySelector('video');
  const button = video.querySelector<HTMLElement>('.video__button');
  
  button?.addEventListener('click', () => {
    if (!videoElem) return;

    videoElem.controls = true;
    button.classList.add('--hidden');
    videoElem.play();
  });
});